import React from 'react'
import { Link } from 'react-router-dom'
import cn from 'classnames'
const Navbar = (props) => {

    return(
        <div className="hero-head">
        <header className="navbar is-transparent">
            <div className="container">
                <div className="navbar-brand">
                    <span className="navbar-burger burger" data-target="navbarMenuHeroC">
                        <span></span>
                        <span></span>
                        <span></span>
                    </span>
                </div>
                <div id="navbarMenuHeroC" className="navbar-menu">
                    <div className="navbar-end">
                        <Link className={cn("navbar-item",  {
                            "has-text-warning" : props.activeTab === 'home'
                        })} to="/" style={{'fontWeight':'bold', 'fontSize': '16px'}}> Home </Link>
                        <Link className={cn("navbar-item",  {
                            "has-text-warning" : props.activeTab === 'facts'
                        })} to="/facts/" style={{'fontWeight':'bold', 'fontSize': '16px'}}> F.A.C.T.S </Link>
                        <Link className={cn("navbar-item",  {
                            "has-text-warning" : props.activeTab === 'rules'
                        })} to="/rules/" style={{'fontWeight':'bold', 'fontSize': '16px'}}> Rules </Link>
                    </div>
                </div>
            </div>
      </header>
    </div>
    )
}

export default Navbar


